import IntakeRecordCard from "./IntakeRecordCard";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import SelectInstituteUniversalBatch from "../../../SelectInstituteUniversalBatch/SelectInstituteUniversalBatch";
import CreateButton from "../../../../Ui/Button/CreateButton";
import style from "../../../FinanceManager/FinanceManager.module.css";
import {
  useAdmissionIntakeRecordList,
  useOneApplicationIntakeExport,
} from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
const IntakeRecord = ({ aid, instituteId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [step, setStep] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [oneApplicationIntakeExport] = useOneApplicationIntakeExport();

  const {
    admissionIntakeRecordList,
    admissionIntakeRecordListLoading,
    admissionIntakeRecordListRefetch,
  } = useAdmissionIntakeRecordList({
    data: {
      aid: aid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !aid,
  });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionIntakeRecordList?.list?.length) {
      let obj = nextPage(admissionIntakeRecordList?.list?.length);
      setState(() => obj);
    }
  }, [admissionIntakeRecordList?.list]);

  const onSearchEvent = (e) => {
    if (
      !admissionIntakeRecordListLoading &&
      admissionIntakeRecordList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = () => {
    setPage(() => 1);
    admissionIntakeRecordListRefetch();
  };
  const onCloseBatch = () => {
    setStep("");
  };
  const onSelectUniversalBatch = (dt) => {
    if (dt?._id && aid) {
      setDisabled((pre) => !pre);
      oneApplicationIntakeExport({
        aid: aid,
        bodyParameter: {
          bid: dt?._id,
        },
      })
        .then(() => {
          //   onRefetch();
          setStep("");
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContentWrapper
        customStyle={{
          padding: "0",
        }}
      >
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
              />
            </div>
          </section>
          <CreateButton
            label="export"
            customStyle={{
              paddingBlock: "0.4rem",
              margin: "0",
            }}
            onAction={() => setStep(1)}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />

      {admissionIntakeRecordList?.list?.map((excel, index) =>
        admissionIntakeRecordList?.list?.length === index + 1 ? (
          <div key={excel?._id} ref={ref}>
            <IntakeRecordCard excel={excel} aid={aid} onRefetch={onRefetch} />
          </div>
        ) : (
          <IntakeRecordCard
            excel={excel}
            key={excel?._id}
            aid={aid}
            onRefetch={onRefetch}
          />
        )
      )}
      {admissionIntakeRecordListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        admissionIntakeRecordList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No intake records found related this search."
          />
        )
      ) : (
        admissionIntakeRecordList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No intake records added."
          />
        )
      )}

      {step === 1 ? (
        <SelectInstituteUniversalBatch
          onSelectStaff={onSelectUniversalBatch}
          onClose={onCloseBatch}
          instituteId={instituteId}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};
export default IntakeRecord;
